<template>
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <div class="vist-distribution flex flex-column">
        <div class="task-distribution flex-1 flex-column">
            <div class="sub-title flex-1">
                <div class="sub-title-icon pie-icon"></div>
                <span class="sub-title-name">拜访任务类型占比</span>
            </div>
            <div class="task-distribution-middle flex flex-14 flex-row">
                <e-pie class="vist-e-pie flex" :id="'e-pie-1'" :pieConfig="pieConfig1" :data="eData3"></e-pie>
            </div>
            <div class="task-distribution-bottom">
                <data-table orderBy="1|desc" :scrollY="220" :isShowTitle="table.isShowTitle" :title="table.tableTitle" :colorList="table.colorList" :data="table.tableData"></data-table>
            </div>
        </div>
    </div>
  </van-pull-refresh>
</template>

<script>
import { PullRefresh } from "vant";
import ePie from "./common/echarts-pie";
import { eData3 } from "./data/echarts-pie-data.js";
import dataTable from "./common/datatables";

export default {
  name: "overview",
  components: {
    [PullRefresh.name]: PullRefresh,
    ePie,
    dataTable
  },
  data() {
    return {
      isLoading: false,
      eData3,
      pieConfig1: {
        imgUrl: '',
        size: "",
        labelShow: true,
        radius:['30%','50%']
      },
      table:{
        isShowTitle: false,
        tableTitle: ["客户名称", "客户数", "拜访次数"],
        colorList:["#448DFF", "#59D4FF", "#751DE2", "#BB2B79", "#DB479B", "#F8B9D9", "#D391B8", "#C2A0F7", "#AEEFFE", "#A7D0FF"],
        tableData: [
          ["拜访", "45%", "45"],
          ["内部事务", "35%", "22"],
          ["外部活动", "25%", "43"],
          ["外出", "15%", "12"],
          ["电话", "45%", "3"]
        ]
      },
    };
  },
  mounted() {
    this.$parent.active = this.findKey(this.$parent.pageMap, this.$route.name);
  },
  methods: {
    findKey(obj, value, compare = (a, b) => a === b) {
      return Object.keys(obj).find(k => compare(obj[k], value));
    },
    onRefresh() {
      setTimeout(() => {
        this.isLoading = false;
        this.$router.replace({
          path: "/refresh",
          query: {
            t: Date.now()
          }
        });
      }, 500);
    }
  }
};
</script>

<style scoped>
.vist-distribution {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.vist-distribution .task-distribution {
  min-height: 300px;
}

.task-distribution-middle{
    min-height: 300px;
}

.task-distribution-bottom{
  min-height: 200px;
  margin: 0 40px 10px 40px;
}
</style>
